.shop-header {
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
}

.shop-header .logo {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 2.5rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  padding-top: 1rem;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  color: cadetblue;
  margin-right: 10px;
}

.scooter-list-item {
  display: flex;
  margin: 15px 0;
}

.scooter-list-item .scooter-cover {
  flex-shrink: 0;
  width: 120px;
  margin-right: 30px;
}

.scooter-list-item .scooter-cover img {
  max-width: 100%;
}

.scooter-list-item .scooter-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 10px;
  align-items: flex-start;
}

.scooter-list-item .scooter-details .scooter-title {
  font-size: 1.2rem;
}

.scooter-list-item .scooter-details .scooter-price {
  font-size: 1.4rem;
}


.scooter-list {
  list-style: none;
}


.shopping-cart-table {
  margin-top: -30px;
}

.shopping-cart-table .total {
  text-align: right;
  font-size: 1.3rem;
  margin-right: 10px;
}

.shopping-cart-table button {
  margin-left: 5px;
  margin-top: 5px;
}
.cart-table-headline {
  display: flex;
  justify-content: center;
  align-content: space-between;
}


